<template>
  <div class="clearfix">
    <!-- metro -->
    <color-block></color-block>
    <da-panel :no-head="true" margin="10px 0 50px">
      <div class="wel-msg">
        <div class="wel-msg-base">
          <P>{{user}}</P>
          <P>{{userDepartment}}</P>
          <P>{{userRole}}</P>
        </div>
        <img src="../../assets/images/logo.png">
        <div class="wel-msg-company">
          <p></p>
          <p>Copyright  2018-2023&nbsp;. All rights reserved.&nbsp;&nbsp;Ver 1.0</p>
        </div>
      </div>
    </da-panel>
    <div class="wel-footer">
      <span @click="redirectMeterAccount" class="link-a">{{onlineCount}}</span>
      <span @click="redirectMeterAccount" class="link-a">{{offlineCount}}</span>
      <span @click="redirectAlarmCentre" class="link-a">{{alarmCount}}</span>
      <span @click="redirectAlarmCentre" class="link-a">{{alarmMessage}}</span>
    </div>
  </div>
</template>

<script>
import colorBlock from "./template/colorBlock";
export default {
  components: {
    colorBlock
  },
  data() {
    return {
      //   
      user: "",
      //   
      userDepartment: "",
      //   
      userRole: "",
      //   
      onlineCount: "",
      //   
      offlineCount: "",
      //   
      alarmCount: "",
      //   
      alarmMessage: "",
      //   
      alarmMessageArr: []
      //   count: 0
    };
  },
  methods: {
    loadData() {
      this.getCurrentUserAccount();
      this.getCurrentUserRoles();
    },
    /**
     * 
     */
    getCurrentUserAccount() {
      this.$ajax.get("rest/user/currentUser").then(res => {
        if (res.data.status) {
          let userDetail = res.data.records;
          this.user = userDetail.name;
          this.userDepartment = userDetail.orgName;
        }
      });
    },
    /**
     * 
     */
    getCurrentUserRoles() {
      this.userRole = "";
      this.$ajax.get("rest/user/currentUserRole").then(res => {
        if (res.data.status) {
          let data = [];
          data = res.data.records;
          data.forEach(role => {
            if (this.userRole === "") {
              this.userRole += role.roleName;
            } else {
              this.userRole += "," + role.roleName;
            }
          });
        }
      });
    },
    /**
     * 
     */
    getTodayAlarm() {
      let now = new Date();
      let startTime = this.$moment(now, "YYYY-MM-DD");
      let endTime = startTime;
      this.alarmCount = 0;
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "", // String,
            userIds: null, // String,ID
            alarmLevels: null, // String,
            alarmGroups: null, // String,ID
            alarmCodes: null, // String,
            receiveTypes: null, // String,
            orgIds: null, // String,ID
            equipIds: null, // String,ID
            productIds: null, // String,ID
            alarmItems: null, // String,ID
            startTime: startTime, // String,
            endTime: endTime, // String,
            isCount: false, // Boolean,
            countCycle: "3", // String,1358
            groupBys: "000000100" // String,111111111[][][][][][][][][]
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            data = res.data.records;
            if (data && data.length > 0) {
              this.alarmCount = data.length;
              data.forEach(item => {
                this.alarmMessageArr.push(item.alarmDetail);
              });
            }
          }
        });
    },
    /**
     * 
     */
    getOnlineMeterCountInfor() {
      this.onlineCount = 0;
      this.offlineCount = 0;
      this.$ajax
        .get("rest/analysisData/onlineMeter", {
          params: {
            title: "", // String,
            orgIds: null, // String,ID
            energySubItems: null, // String,ID
            energyIds: null, // String,ID
            isOnline: null, // ,
            isCollection: false, // ,/
            isCount: true, // ,
            groupBys: "00010" // String,11111[][][][][],1
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            data = res.data.records;
            if (data && data.length > 0) {
              data.forEach(item => {
                if (item.online === 1) {
                  this.onlineCount = item.num;
                } else {
                  this.offlineCount = item.num;
                }
              });
            }
          }
        });
    },
    redirectMeterAccount() {
      this.$router.push({ path: "/tabulationEquipmentLedger" });
    },
    redirectAlarmCentre() {
      this.$router.push({ path: "/alarmCentre" });
    }
  },
  mounted() {
    this.loadData();
    this.getTodayAlarm();
    this.getOnlineMeterCountInfor();

    /*     setInterval(() => {
      this.alarmMessage = this.alarmMessageArr[this.count];
      this.count++;
      if (this.count == 4) {
        this.count = 0;
      }
    }, 2000); */
  }
};
</script>
<style scoped>
.wel-msg {
  display: flex;
  width: 100%;
  padding: 10px;
  align-items: flex-end;
}

.wel-msg-base {
  font-size: 16px;
  line-height: 33.3px;
}
.wel-msg-company {
  line-height: 2em;
}
.wel-msg > div {
  flex: 1;
}
.wel-msg > img {
  width: auto;
  height: 100px;
}
.wel-footer {
  position: fixed;
  bottom: 0;
  background: #fff;
  width: 100%;
  margin-left: -30px;
  padding: 10px;
  border-top: 1px solid #ddd;
  background-color: #eee;
  /* z-index: -1; */
}
.wel-footer > span {
  padding: 0 10px;
}
</style>



